import Vue from 'vue'
import Router from 'vue-router'
import bottom from '@/components/bottom'
import dining from '@/components/dining'
import kitchen from '@/components/kitchen'
import socialize from '@/components/socialize'
import mine from '@/components/mine'
import breakfast from '@/components/breakfast'
import snacks from '@/components/snacks'
import dinner from '@/components/dinner'
import order from '@/components/order'
import posted from '@/components/posted'
import registration from '@/components/registration'
import personal from '@/components/personal'
import login from '@/components/login'
import mp from '@/components/mp'
import setting from '@/components/setting'
import sc from '@/components/sc'
import address from '@/components/address'
import qqq from '@/components/qqq'
import xiaoxi from '@/components/xiaoxi'
import xqing from '@/components/xqing'
Vue.use(Router)



export default new Router({
	routes: [{
			path: '/',
			name: 'bottom',
			component: bottom,
			children: [{
					path: '/dining',
					name: 'dining',
					component: dining,
					children: [{
							path: '/breakfast',
							name: 'breakfast',
							component: breakfast,
						},
						{
							path: '/snacks',
							name: 'snacks',
							component: snacks
						},
						{
							path: '/dinner',
							name: 'dinner',
							component: dinner,

						},

					]
				},
				{
					path: '/kitchen',
					name: 'kitchen',
					component: kitchen
				},
				{
					path: '/socialize',
					name: 'socialize',
					component: socialize
				},
				{
					path: '/mine',
					name: 'mine',
					component: mine,
				},
				{
					path: '/order',
					name: 'order',
					component: order
				},
				{
					path: '/posted',
					name: 'posted',
					component: posted
				},
				{
					path: '/personal',
					name: 'personal',
					component: personal
				}
			]
		},
		{
			path: '/registration',
			name: 'registration',
			component: registration
		},
		{
			path: '/login',
			name: 'login',
			component: login
		},

		{
			path: '/mp',
			name: 'mp',
			component: mp
		},
		{
			path: '/setting',
			name: 'setting',
			component: setting
		},
        {
			path: '/sc',
			name: 'sc',
			component: sc
		},
		 {
			path: '/address',
			name: 'address',
			component: address
		},
		{
			path: '/qqq',
			name: 'qqq',
			component: qqq
		},
		{
			path: '/xiaoxi',
			name: 'xiaoxi',
			component: xiaoxi
		},
		{
			path: '/xqing',
			name: 'xqing',
			component: xqing
		},
	]
})


