export default {
	data() {
		return {
			showPrompting: false,
			prompting: false,
			newUsername:'',
			newPassword:'',
		}
	},
	methods: {
		registration(){
			  
			if(this.newUsername==''|| this.newPassword==''){
				alert('请输入用户名和密码')
			}
				
			
			else{
				let data={'username':this.newUsername,'password':'newPassword'}
				this.$http.post('./src/login.php',data).then((res)=>{
					console.log(res)
					if(res.data=="ok"){
						this.prompting='注册成功'
						this.showPrompting='true'
						this.username=''
						this.password=''
						//注册成功跳回登录页面
						setTimeout(function(){
							this.showPrompting=false
							this.showLogin=true
							this.showPrompting=false
						}.bind(this),1000)
					}
				})
			}
		}
	}
	
}

