import { setCookie, getCookie } from "../assets/js/cookie.js"
export default {
	data() {
		return {
			showPrompting: false,
			prompting: false,
			username: '',
			password: '',
		}
	},
	mounted() {
		if(getCookie('uesrname')) {
			this.$router.push('/breakfast')
		}
	},
	methods: {
		login() {
			 var c = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/
             var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			if(this.username == '' || this.password == '') {
				alert("请输入用户名或密码")
			}else if(!reg.test(this.username)){
				alert('格式有误')
				return
			}else if(!c.test(this.password)){
				alert('密码至少一个大写字母，一个小写字母，一个数字，其他可以是字符')
				return
			}
			else {
				let data = {
					'username': this.username,
					'password': this.password
				}
				//接口请求
				this.$http.post('./src/login.php', data).then((res) => {
					console.log(res)
					//接口传值(-1：用户不存在；0：密码错误)，同时检测管理员账号的值
					if(res.data == -1) {
						this.prompting = '该用户不存在'
						this.showPrompting = true
					} else if(res.data == 0) {
						this.prompting = '密码输入错误'
						this.showPrompting = true
					} else if(res.data == 'admin') {
						//路由跳转
						this.$router.push('/main')
					} else {
						this.prompting = '登录成功'
						this.showPrompting = true
						setCookie('username', this.username, 1000 * 60)
						setTimeout(function() {
							this.$router.push('/breakfast')
						}.bind(this), 1000)
					}
				})
			}
		}
	}
}



