/* harmony default export */ var mp = ({
	name: '',
	data: function data() {
		return {};
	},
	mounted: function mounted() {
		// 百度地图API功能
		// 创建Map实例
		var map = new BMap.Map("XSDFXPage", {
			enableMapClick: true
		});
		// 初始化地图,设置中心点坐标和地图级别
		map.centerAndZoom(new BMap.Point(116.4035, 39.915), 11);
		// 添加地图类型控件
		map.addControl(new BMap.MapTypeControl());
		// 设置地图显示的城市 此项是必须设置的
		map.setCurrentCity("杭州");
		// 开启鼠标滚轮缩放      
		map.enableScrollWheelZoom(true);
		//添加工具条、比例尺控件
		/*************************************************/
		map.addControl(new BMap.ScaleControl({
			anchor: BMAP_ANCHOR_TOP_LEFT
		}));
		map.addControl(new BMap.NavigationControl());
		map.addControl(new BMap.NavigationControl({
			anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
			type: BMAP_NAVIGATION_CONTROL_SMALL
		}));
		/************************************************
  添加自定义控件类，放大ZoomControl
  *************************************************/
		function ZoomControl() {
			//默认停靠位置和偏移量
			this.defaultAnchor = BMAP_ANCHOR_BOTTOM_RIGHT;
			this.defaultOffset = new BMap.Size(50, 23);
		}
		//通过JavaScript的prototype属性继承于BMap.Control
		ZoomControl.prototype = new BMap.Control();
		//自定义控件必须实现自己的initialize方法，并且将控件的DOM元素返回
		//在本方法中创建div容器，并将其添加到地图容器中
		ZoomControl.prototype.initialize = function (map) {
			//创建一个DOM元素
			var div = document.createElement('div');
			//添加文字说明
			div.appendChild(document.createTextNode('放大两级'));
			//添加样式
			div.style.color = '#40C5CC';
			div.style.cursor = 'pointer';
			div.style.border = '3px solid gray';
			div.style.backgroundColor = '#eee';
			//绑定事件，点击触发
			div.onclick = function (e) {
				map.setZoom(map.getZoom() + 2);
			};
			//添加DOM元素到地图上
			map.getContainer().appendChild(div);
			//将DOM元素返回
			return div;
		};
		//创建控件
		var myZoomCtrl = new ZoomControl();
		map.addControl(myZoomCtrl);
		//定义新图标
		var myIcon = new BMap.Icon("http://developer.baidu.com/map/jsdemo/img/fox.gif", new BMap.Size(300, 157));
		//文字提示
		var label = new BMap.Label('广州西站', {
			offset: new BMap.Size(140, 10)
		});
		//给地图添加右键菜单
		/*************************************************/
		//创建搜索实例，并将结果展现在地图实例上 
		var local = new BMap.LocalSearch(map, {
			renderOptions: {
				map: map,
				panel: "results" //搜索结果自动添加到搜索结果列表容器中 
			} });
		local.search("北京"); //搜索长沙 
		var menu = new BMap.ContextMenu();

		var txtMenuItem = [{
			text: '放大',
			callback: function callback() {
				map.zoomIn();
			}
		}, {
			text: '缩小',
			callback: function callback() {
				map.zoomOut();
			}
		}];
		for (var i = 0; i < txtMenuItem.length; i++) {
			menu.addItem(new BMap.MenuItem(txtMenuItem[i].text, txtMenuItem[i].callback, 100));
		}
		map.addContextMenu(menu);
	}
});
