import {setCookie,getCookie,delCookie} from '../assets/js/cookie.js'
export default {
	data() {
		return {
			name:'',
			img: "./src/assets/img/portrait.jpg",
			nickname: '就是爱吃',
			grade: '吃货',
			age: '22',
			declaration: '人生唯有美食牵动我心'
		}
	},
	mounted(){
		//挂载保存的Cookie,渲染
		let uname=getCookie('username')
		this.name=uname
		//CooKie不存在，切换登录页面
		if(uname==''){
			this.$router.push('/')
		}
	},
	methods:{
		quit(){
			//删除Cookie
			delCookie('username')
		}
	},
	components: {}
}



